@import './Common.scss';

#root {
	> nav,
	> .content > .menu,
	> .content > .cover {
		z-index: 2;
	}

	> nav > .logo,
	> .content > .menu > .top > .logo {
		display: flex;
		align-items: center;
		justify-content: center;
		height: var(--nav-height);
		padding: 0px 15px;
		color: inherit;
		white-space: nowrap;

		> svg {
			width: inherit;
			height: inherit;
		}

		width: 35px;
	}

	> nav > .button,
	> .content > .menu > .top > .button {
		box-sizing: content-box;
		border: none;
		background: none;
		width: 20px;
		height: 20px;
		margin: auto 5px;
		border-radius: 2px;
		padding: 5px;
		position: relative;
		flex: none;
		cursor: pointer;
		color: var(--text);

		> svg {
			font-size: 20px;
		}

		&:hover {
			background: var(--background-accent);
		}
	}

	> nav {
		display: flex;
		height: var(--nav-height);
		width: 100%;
		box-sizing: border-box;
		align-content: center;
		position: fixed;
		color: var(--text);
		border-bottom: var(--border) 1px solid;
		padding: 0px 18px;
		text-align: center;
		background: var(--background);
		align-items: center;

		> .collapsable {
			display: flex;
			flex-direction: row;
		}

		@include screen_mobile {
			> .shift-right {
				display: none;
			}

			justify-content: space-between;

			> .logo {
				position: absolute;
				left: 0px;
				right: 0px;
				margin: auto;
			}
		}

		> .shift-right {
			margin-left: auto;
		}
	}

	> .content {
		padding-top: var(--nav-height-border);
		flex-direction: column;
		min-height: calc(100vh - var(--nav-height-border));
		overflow: hidden;
		display: flex;

		> .cover {
			background-color: rgba(0, 0, 0, 0.5);
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			transition: opacity 0.3s, visibility 0.3s;
			opacity: 0;
			visibility: hidden;
		}

		> .menu {
			position: fixed;
			flex-direction: column;
			display: flex;
			visibility: visible;
			background-color: var(--background);
			border-right: 1px solid var(--border);
			left: 0;
			top: 0;
			width: 300px;
			height: 100vh;
			box-sizing: border-box;
			transform: translateX(-100%);
			transition: transform 0.3s, visibility 0.3s 0.3s;
			overflow: hidden;

			@include screen_mobile {
				width: 100vw;
				height: 100vh;
			}

			> .top {
				padding-left: 18px;
				height: var(--nav-height);
				box-sizing: border-box;
				border-bottom: var(--border) 1px solid;
				width: inherit;
				display: flex;
				flex-direction: row;

				> .logo {
					display: flex;
					align-items: center;
					justify-content: center;
					height: var(--nav-height);
					padding: 0px 15px;
					color: inherit;
					white-space: nowrap;

					> svg {
						width: inherit;
						height: inherit;
					}

					width: 35px;
				}
			}
		}

		.menu-list {
			padding: 6px 5px 0px 0px;
			flex-direction: column;
			overflow-y: auto;
			scrollbar-width: thin;
			height: 100%;
			user-select: none;
			text-align: left;

			> .entry {
				background-color: transparent;
				border: none;
				color: var(--contrast-background);
				display: flex;
				align-items: center;
				font: inherit;
				font-size: 14px;
				height: 24px;
				padding: 10px 25px;
				border-top-right-radius: 5px;
				border-bottom-right-radius: 5px;

				&[data-selected='1'] {
					background-color: var(--background-accent-deep-1);
					> .name {
						font-weight: bold;
					}
				}

				> .icon {
					width: 24px;
					height: 24px;
					margin-right: 15px;
					display: flex;
					justify-content: center;
					align-items: center;

					> svg {
						width: inherit;
						height: inherit;
					}
				}

				&:hover {
					color: var(--text);
					background: var(--background-accent);

					&[data-selected='1'] {
						background: var(--background-accent);
					}
				}
			}

			> .title {
				font-size: 14px;
				font-weight: bold;
				padding: 10px 25px;
				color: var(--contrast-background);
			}

			> .bar {
				margin: 10px 0px;
				width: 100%;
				border-bottom: 1px solid var(--text-accent);
			}

			> .genres {
				display: flex;
				flex-wrap: wrap;
				flex-direction: row;
				padding: 10px 25px;

				> .entry {
					display: flex;
					align-items: center;
					justify-content: center;
					padding: 2px 8px;
					margin: 2px;
					height: 20px;
					border: 1px solid var(--border);
					border-radius: 5px;
					font-size: 13px;
					font-family: 'Montserrat Alternates';
					color: var(--text-placeholder);
					white-space: nowrap;

					&:hover {
						background-color: var(--background-accent);
					}
				}
			}
		}
	}
}

html[data-expanded='1'] {
	body {
		overflow: hidden;
	}

	#root {
		> .content {
			> .menu {
				transform: translateX(0%);
			}

			> .cover {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}
